.background-child,
.wave-icon {
  position: absolute;
  right: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.background-child {
  height: 99.87%;
  width: 100%;
  top: 0;
  bottom: 0.13%;
  left: 0;
  object-fit: cover;
}
.wave-icon {
  height: 23.62%;
  width: 99.45%;
  top: 76.38%;
  bottom: 0;
  left: 0.55%;
}
.span {
  color: var(--color-tomato-100);
}
.background,
.microtitle {
  position: absolute;
  top: 135px;
  left: 619px;
}
.background {
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  text-align: left;
  color: var(--white);
}
.shadow {
  width: calc(100% + 0.3px);
  right: -0.3px;
  border-radius: var(--br-1981xl);
}
.background1,
.fill,
.shadow {
  position: absolute;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
}
.fill {
  width: calc(100% + 0.3px);
  right: -0.3px;
  border-radius: var(--br-1981xl);
  background-color: var(--color-gray-100);
}
.background1 {
  width: 100%;
  right: 0;
  border-radius: var(--br-5xs);
  overflow: hidden;
}
.content,
.label {
  position: absolute;
  top: calc(50% - 12px);
}
.label {
  width: 100%;
  left: 0;
  display: inline-block;
}
.content {
  left: calc(50% - 66.25px);
  width: 132.4px;
  height: 24px;
  overflow: hidden;
}
.button,
.talent {
  color: var(--color-tomato-100);
}
.button {
  position: absolute;
  height: calc(100% - 685px);
  width: calc(100% - 1267.3px);
  top: 434px;
  right: 625.2px;
  bottom: 251px;
  left: 642.1px;
}
.talent {
  text-decoration: underline;
}
.experience-you-can-container {
  position: absolute;
  width: 56.35%;
  top: 25.1%;
  left: 23.36%;
  font-size: 56px;
  letter-spacing: 0.2px;
  line-height: 68px;
  display: inline-block;
}
.from-year-to {
  position: absolute;
  width: 55.46%;
  top: calc(50% - 28.5px);
  left: 23.02%;
  font-size: 20px;
  line-height: 36px;
  display: inline-block;
}
.hero {
  width: 100%;
  position: relative;
  height: 741px;
  text-align: center;
  font-size: var(--body-large-size);
  color: var(--color-gray-100);
  font-family: var(--body-large);
}
